.app-header.navbar {
    position: relative;
    flex-direction: row;
    height: $navbar-height;
    padding: 0;
    margin: 0;
    background-color: $navbar-bg;
    @include borders($navbar-border);
  
    .navbar-brand {
      display: inline-block;
      width: $navbar-brand-width;
      height: $navbar-height;
      padding: $navbar-padding-y $navbar-padding-x;
      margin-right: 0;
      background-color: $navbar-brand-bg;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: $navbar-brand-logo-size;
      @include borders($navbar-brand-border);
    }
  
    .navbar-toggler {
      min-width: 50px;
      padding: $navbar-toggler-padding-y 0;
  
      &:hover .navbar-toggler-icon {
        background-image: $navbar-toggler-icon-hover;
      }
    }
  
    .navbar-toggler-icon {
      height: 23px;
      background-image: $navbar-toggler-icon;
    }
  
    .navbar-nav {
      flex-direction: row;
      align-items: center;
    }
  
    .nav-item {
      position: relative;
      min-width: 50px;
      margin: 0 !important;
      text-align: center;
  
      button {
        margin: 0 auto;
      }
  
      .nav-link {
        padding-top: 0;
        padding-bottom: 0;
        background: 0;
        border: 0;
  
        .badge {
          position: absolute;
          top: 50%;
          left: 50%;
          margin-top: -16px;
          margin-left: 0;
        }
  
        > .img-avatar {
          height: $navbar-height - 20px;
          margin: 0 10px;
        }
      }
    }
  
    .dropdown-menu {
      padding-bottom: 0;
      line-height: $line-height-base;
    }
  
    .dropdown-item {
      min-width: 180px;
    }
  }
  
  .navbar-brand {
    color: $navbar-active-color;
  
    @include hover-focus {
      color: $navbar-active-color;
    }
  }
  
  .navbar-nav {
    .nav-link {
      color: $navbar-color;
  
      @include hover-focus {
        color: $navbar-hover-color;
      }
    }
  
    .open > .nav-link,
    .active > .nav-link,
    .nav-link.open,
    .nav-link.active {
      @include plain-hover-focus {
        color: $navbar-active-color;
      }
    }
  }
  
  .navbar-divider {
    background-color: rgba(0,0,0,.075);
  }
  
  @include media-breakpoint-up(lg) {
    .brand-minimized {
      .app-header.navbar {
        .navbar-brand {
          width: $navbar-brand-minimized-width;
          background-color: $navbar-brand-minimized-bg;
          background-size: $navbar-brand-minimized-logo-size;
          @include borders($navbar-brand-minimized-border);
        }
      }
    }
  }